import React from 'react'
import { withRouteData, Link } from 'react-static'
//

export default withRouteData(({posts}) => {
  const cleanPosts = posts.filter(p => p.title);
  return (
    <div>
      <h1>Blog</h1>
      <br />
      All Posts:
      <ul>
        {cleanPosts.map(post => (
          <li key={post.slug}>
            <Link to={`/blog/${post.slug}/`}>{post.title}</Link>
          </li>
        ))}
      </ul>
    </div>
  )
})
